* {
  margin: 0;
  padding: 0;
}

.app {
  min-width: 400px;
  margin: auto;
  padding: 20px;
  text-align: center;
}

.app .date {
  margin: 20px 0 10px 0;
  display: flex;
  }

.app .date p {
  padding: 0 2px;
  font-size: 16px;
  font-weight: 500;
}

.app .form-input {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app .form-input .icon {
  position: absolute;
  left: 15px;
  top: 0;
  font-size: 25px;
  color: darkblue;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
 
} 

.app form input {
  border: 2px solid #eee;
  padding: 5px 10px 5px 50px;
  font-size: 18px;
  line-height: 30px;
  outline: 0; 
  display: block;
  width: 100%;
  border-radius: 8px;
}

.app .task-row {
  border: 2px solid #eee;
  padding: 10px;
  font-size: 18px;
  line-height: 30px;
  margin: 8px 0;
  width: 99%;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.app .task-row .icon {
  font-size: 25px;
  color: darkblue
}

.app .task-row p {
  text-transform: capitalize;
}

.completed {
  background-color: #eee;
  text-decoration: line-through;
}

.app .length {
  margin: 10px 0;
  font-size: 18px;
}